.simon__container {
  border-radius: 2%;
  margin: auto;
  height: 80vh;
  padding: 5px;
  display: grid;
  grid-template-rows: 0.2fr 0.01fr 1fr;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  padding: 1%;
}
.simon__container .simon__topHalf {
  width: 95%;
  margin: auto;
  border: 1px solid black;
  display: grid;
  grid-template-columns: 1fr 1fr;
  border: 1px solid lightgray;
  border-radius: 5px;
  margin-top: 1%;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.simon__container .simon__topHalf .simon_settings {
  padding: 10px;
  display: flex;
  flex-direction: column;
  border-right: 1px solid white;
}
.simon__container .simon__topHalf .simon_settings .simon__titleBox {
  display: flex;
  flex-direction: row-reverse;
}
.simon__container .simon__topHalf .simon_settings .simon__buttons .simon_leaderboard {
  align-self: center;
  justify-self: center;
}
.simon__container .simon__topHalf .simon__title {
  width: 100%;
  text-align: center;
}
.simon__container .simon__topHalf .simon__gameInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-left: 1px solid white;
}
.simon__container .simon__topHalf .simon__start {
  margin: 5px;
  cursor: pointer;
}
.simon__container .simon__topHalf .simon__stop {
  margin: 5px;
  cursor: pointer;
}
.simon__container .simon__topHalf .simon__soundToggle {
  float: right;
  grid-area: sound;
  cursor: pointer;
}
.simon__container .simon__turn {
  width: 95%;
  margin: auto;
  border: 1px solid lightgray;
  display: flex;
  justify-content: center;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.simon__container .simon__turn .gameOverGuest {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.simon__container .simon__boxesContainer {
  width: 95%;
  margin: 0 auto;
  border: 1px solid lightgray;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.simon__container .simon__boxesContainer .simon_boxes {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 50%;
  height: 93%;
  margin: auto;
  margin-top: 1%;
  grid-gap: 1%;
  justify-items: center;
  border-radius: 45%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  overflow: hidden;
}
.simon__container .simon__boxesContainer .simon_boxes .boxes_box {
  cursor: pointer;
  width: 100%;
  height: 100%;
}
.simon__container .simon__boxesContainer .simon_boxes .yellow {
  background-color: yellow;
}
.simon__container .simon__boxesContainer .simon_boxes .red {
  background-color: red;
}
.simon__container .simon__boxesContainer .simon_boxes .blue {
  background-color: blue;
}
.simon__container .simon__boxesContainer .simon_boxes .green {
  background-color: green;
}

@media only screen and (max-width: 744px) {
  .simon__container .simon__gameInfo .gameinfo1 .gameinfo2 h2 {
    font-size: 12px;
  }
  .simon__container .simon__boxesContainer .simon_boxes {
    width: 80%;
    height: 60%;
    grid-gap: 1%;
    margin: 3% auto;
  }
  .simon__container .simon__boxesContainer .simon_boxes .boxes_box {
    height: 100%;
    width: 100%;
  }
}