.mainContainer {
  height: 100vh;
}

.login__Container .navbarFather {
  border-bottom: 1px solid lightgray;
}
.login__Container .navbarFather .navbarContainer {
  width: 100%;
}
.login__Container .navbarFather .navbarContainer .navbarContent {
  display: flex;
  justify-content: center;
  border-radius: 5px;
}
.login__Container .navbarFather .navbarContainer .navbarContent .nav-link {
  border: 1px solid lightgray;
  border-radius: 5px;
  margin: 5px;
}

.loginForm {
  width: 60%;
  padding: 2%;
  border-radius: 5px;
  border: 1px solid lightgray;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.meter {
  display: none;
}