.wrapper {
  height: 100vh;
  margin: auto;
  margin-top: 1%;
  display: grid;
  overflow-y: scroll;
  grid-template-rows: 1fr 1fr;
  border-radius: 5px;
}
.wrapper .hangman__infoContainer {
  height: 100%;
}
.wrapper .hangman__infoContainer .hangman__start {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.wrapper .hangman__infoContainer .hangman__start .btn {
  margin: auto;
}
.wrapper .hangman__infoContainer .hangman__start .title {
  text-align: center;
}
.wrapper .hangman__infoContainer .hangman__liveGame {
  height: 65%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-bottom: 1px solid lightgray;
}
.wrapper .hangman__infoContainer .hangman__liveGame .btn {
  margin: auto;
}
.wrapper .hangman__infoContainer .hangman__liveGame .title {
  text-align: center;
}
.wrapper .noLoggedIn {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.wrapper .noLoggedIn .winloseMsg {
  text-align: center;
}
.wrapper .noLoggedIn .loginRequest {
  text-align: center;
}
.wrapper .hangman__stats {
  display: flex;
  justify-content: center;
}
.wrapper .winningInfo {
  text-align: center;
}
.wrapper .table {
  border: 1px solid lightgray;
}
.wrapper .hangman__game {
  height: 100%;
  padding: 5px;
  margin-top: -12%;
}
.wrapper .gameInfo {
  text-align: center;
  font-size: 20px;
}
.wrapper .gameInfo #toot {
  font-size: 30px;
}
.wrapper .lowerWrapper {
  margin-top: 5%;
  display: grid;
  grid-template-columns: 1fr 0.65fr;
}
.wrapper .lowerWrapper #hangman {
  display: flex;
  justify-content: center;
}
.wrapper .lowerWrapper #hangman img {
  width: 85%;
}
.wrapper .lowerWrapper .letters {
  display: flex;
  justify-items: center;
  height: 50%;
  width: 100%;
  margin: auto;
}
.wrapper .lowerWrapper .letters #letters {
  height: 100%;
  width: 95%;
  display: grid;
  grid-template-areas: "q w e r t y u i o p" ". a s d f g h j k l" ". . z x c v b n m .";
}
.wrapper .lowerWrapper .letters #letters #buttonLetter {
  border: none;
  color: white;
  background-color: #008CBA;
  padding: 15% 15%;
  border-radius: 0.4em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 2% 4%;
  cursor: pointer;
  outline: 0;
}
.wrapper .lowerWrapper .letters #letters #buttonLetter:hover {
  background-color: #005e7e;
}

@media only screen and (max-width: 570px) {
  .letters {
    display: flex;
    flex-direction: column;
    justify-items: center;
    height: 100%;
    width: 100%;
    margin: auto;
  }
  .letters #letters {
    width: 100%;
    height: 100%;
  }
}
@media only screen and (max-width: 360px) {
  .wrapper {
    height: 90vh;
    margin: auto;
    grid-column-gap: 1%;
    display: grid;
    grid-template-rows: 1fr 1fr;
  }
}