@keyframes hover {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-3px);
  }
  75% {
    transform: translateY(3px);
  }
  100% {
    transform: translateY(0);
  }
}
@media only screen and (max-width: 531px) {
  .bgLogo {
    margin-top: 10%;
  }

  .hover {
    display: none;
  }
}
